<template>
  <v-dialog v-model="dialog" persistent max-width="290">
    <v-card>
      <v-card-title class="headline pink--text">
        {{ title }}
      </v-card-title>
      <v-card-text>
        {{ content }}
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="pink darken-1" text @click="onClickCancel()">Hủy</v-btn>
        <v-btn color="pink darken-1" text @click="onClickOK()">Xóa</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "ConfirmDialogComponent",
  props: ['title', 'content', 'dialog'],
  methods: {
    onClickOK() {
      this.$emit('update:dialog', false);
      this.$emit('onAgree');
    },
    onClickCancel() {
      this.$emit('update:dialog', false);
    }
  }
}
</script>

