<template>
  <div class="wrapper">
    <header-component :title="dataClass.className" icon="mdi-arrow-left"
                      @onClickNavIcon="$router.back()"></header-component>
    <v-container id="content-wrapper">
      <div class="btn-wrapper">
        <v-btn color="pink" class="white--text button-custom" @click="gotoAttendance()">Điểm danh</v-btn>
      </div>
      <div class="btn-wrapper">
        <v-btn v-if="isNewClass" color="pink" class="white--text button-custom" @click="gotoEdit()">Sửa / Thêm học
          sinh
        </v-btn>
        <v-btn v-else color="pink" class="white--text button-custom" @click="gotoEdit()">Thêm học sinh</v-btn>
      </div>
      <div class="btn-wrapper">
        <v-btn color="pink" class="white--text button-custom" @click="isDisplayDialogConfirm = true">Xóa lớp học</v-btn>
      </div>
      <v-card class="mt-5">
        <v-banner class="pink--text font-weight-bold">
          Giá tiền 1 buổi
        </v-banner>
        <v-row align="center">
          <v-col cols="8" offset="1">
            <v-text-field label="Nhập giá tiền - vnd" v-model="dataClass.price"></v-text-field>
          </v-col>
          <v-col cols="2">
            <v-btn icon large color="pink" raised @click="savePrice(dataClass.price)">
              <v-icon> mdi-plus-circle</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-container>
    <confirm-dialog-component @onAgree="removeClass()" :dialog.sync="isDisplayDialogConfirm"
                              title="Chắc chắn chưa ?"
                              content="Sau khi xóa dữ liệu lớp học sẽ không thể lấy lại được đâu nha !!">
    </confirm-dialog-component>
    <snackbar-component :text.sync="errorMessage"></snackbar-component>
  </div>
</template>

<script>

import db from "@/script/firestore";
import ConfirmDialogComponent from "@/components/ulti/ConfirmDialogComponent";
import SnackbarComponent from "@/components/ulti/SnackbarComponent";
import HeaderComponent from "@/components/ulti/HeaderComponent";
/* eslint-disable no-debugger */
export default {
  name: "ClassDetailComponent",
  components: {
    ConfirmDialogComponent,
    SnackbarComponent,
    HeaderComponent
  },
  firestore: {
    chickenClass: db.collection('chickenClass'),
  },
  data() {
    return {
      isDisplayDialogConfirm: false,
      errorMessage: '',
      students: {},
      dataClass: {},
      price: '',
    }
  },
  created() {
    const classId = this.$route.params.classId;
    this.$bind('students', db.collection('chickenClass').doc(classId).collection('data'));
    this.$bind('dataClass', db.collection('chickenClass')
        .doc(classId));
  },
  computed: {
    isNewClass() {
      return this.students.length > 0;
    }
  },
  methods: {
    gotoEdit() {
      const classId = this.$route.params.classId;
      this.$router.push({name: 'edit', params: {classId: classId}});
    },
    gotoAttendance() {
      const classId = this.$route.params.classId;
      this.$router.push({name: 'attendance', params: {classId: classId}});
    },
    removeClass() {
      const classId = this.$route.params.classId;
      try {
        db.collection('chickenClass').doc(classId).delete();
        this.$router.back();
      } catch (err) {
        this.errorMessage = err;
      }
    },
    savePrice(price) {
      const classId = this.$route.params.classId;
      try {
        db.collection('chickenClass').doc(classId).update({
          price: price
        });
      } catch (err) {
        this.errorMessage = err;
      }
    }
  }
}
</script>

<style scoped>
#content-wrapper {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.button-custom {
  width: 80vw !important;
  height: 60px !important;
}

.btn-wrapper {
  text-align: center;
  margin: 0 10px 40px 10px;
}
</style>
