<template>
  <v-snackbar v-model="snackbar">
    {{ text }}
    <v-btn color="pink" text @click="$emit('update:text', '')">
      Close
    </v-btn>
  </v-snackbar>
</template>

<script>

export default {
  name: "SnackbarComponent",
  props: ['text'],
  computed: {
    snackbar() {
      return !!this.text;
    }
  },
}
</script>

