<template>
  <div class="wrapper">
    <header-component title="Điểm danh" icon="mdi-arrow-left"
                      @onClickNavIcon="$router.back()"></header-component>
    <v-container id="container">
      <v-row id="student-list" v-for="student in students" :key="student.id" align="center">
        <v-col cols="12">
          <v-card>
            <v-row>
              <v-col cols="9" offset="1">{{ student.studentName }}</v-col>
              <v-col cols="2">
                <!--                <input type="checkbox" :checked="getAttendance(student)" @change="onAttendance(student, $event)">-->
                <v-btn icon color="pink" v-if="getAttendance(student)" @click="onAttendance(student, false)">
                  <v-icon>mdi-heart</v-icon>
                </v-btn>
                <v-btn icon color="pink" @click="onAttendance(student, true)" v-else>
                  <v-icon>mdi-heart-outline</v-icon>
                </v-btn>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <snackbar-component :text.sync="errorMessage"></snackbar-component>
  </div>
</template>

<script>
import db from "@/script/firestore";
import HeaderComponent from "@/components/ulti/HeaderComponent";
import SnackbarComponent from "@/components/ulti/SnackbarComponent";
/* eslint-disable no-debugger */
export default {
  name: "AttendanceComponent",
  components: {
    HeaderComponent,
    SnackbarComponent
  },
  created() {
    const classId = this.$route.params.classId;
    this.$bind('students', db.collection('chickenClass').doc(classId).collection('data'));
  },
  data() {
    const now = new Date();
    const year = now.getFullYear();
    const month = now.getMonth() + 1;
    const key = `${year}_${month}`;
    const date = now.getDate();
    return {
      students: [],
      errorMessage: '',
      key: key,
      date: date
    }
  },
  computed: {
    studentsRef() {
      return db.collection('chickenClass').doc(this.$route.params.classId).collection('data');
    }
  },
  methods: {
    async onAttendance(student, checked) {
      student.data = student.data || {};
      let data = student.data;
      let attendanceMonth = data[this.key] || [];
      if (checked) {
        attendanceMonth.push(this.date)
      } else {
        attendanceMonth = attendanceMonth.filter((v) => v !== this.date);
      }
      student.data[this.key] = attendanceMonth;
      try {
        await this.studentsRef.doc(student.id).update({
          data: student.data
        })
      } catch (err) {
        this.errorMessage = err;
      }
    },
    getAttendance(student) {
      if (student.data && student.data[this.key]) {
        return student.data[this.key].includes(this.date)
      }
      return false;
    }
  }
}
</script>

<style scoped>
#container {
  height: 90vh;
  overflow: scroll;
}
</style>
