<template>
  <div class="wrapper">
    <header-component title="Danh sách lớp học" icon="" @onClickNavIcon="{}"></header-component>
    <v-container class="home-wrapper">
      <v-card class="mt-5">
        <v-banner class="pink--text font-weight-bold">
          Tạo lớp học
        </v-banner>
        <v-row align="center">
          <v-col cols="8" offset="1">
            <v-text-field label="Nhập tên lớp học" v-model="newClassName"></v-text-field>
          </v-col>
          <v-col cols="2">
            <v-btn icon large color="pink" raised @click="addClass()">
              <v-icon> mdi-plus-circle</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-card>
      <v-card class="mt-7" id="class-list-wrapper">
        <v-banner class="pink--text font-weight-bold">
          Danh sách lớp học
        </v-banner>
        <v-container fluid id="class-list">
          <v-row v-for="classData in chickenClass" :key="classData.id" class="mb-1">
            <v-col cols="8" offset="2">
              <v-btn color="pink" class="white--text" block @click="selectClass(classData)">
                {{ classData.className }}
              </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
      <snackbar-component :text.sync="errorMessage"></snackbar-component>
    </v-container>
  </div>
</template>

<script>
import db from '../script/firestore'
import SnackbarComponent from "@/components/ulti/SnackbarComponent";
import HeaderComponent from "@/components/ulti/HeaderComponent";

export default {
  name: 'HomeComponent',
  components: {
    SnackbarComponent,
    HeaderComponent
  },
  data() {
    return {
      newClassName: '',
      errorMessage: '',
      chickenClass: [],
    }
  },
  firestore: {
    chickenClass: db.collection('chickenClass'),
  },
  methods: {
    async addClass() {
      const data = (await db.collection('chickenClass')
          .get()).docs.map(doc => doc.data());
      let isExistClass = data.find((v) => {
        return v.className === this.newClassName;
      });
      if (isExistClass) {
        this.errorMessage = 'Lớp học này có rùi';
      } else if (!this.newClassName) {
        this.errorMessage = "Tên lớp học không để trống nha!";
      } else {
        try {
          await db.collection('chickenClass').add({
            className: this.newClassName
          });
          this.newClassName = '';
          this.errorMessage = '';
        } catch (err) {
          this.errorMessage = err;
        }
      }
    },

    selectClass(classData) {
      this.$router.push({name: 'class', params: {classId: classData.id, className: classData.className}})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#class-list-wrapper {
  height: 63vh;
}

#class-list {
  overflow: scroll;
  height: 50vh;
}
</style>
