<template>
  <div class="wrapper">
    <header-component :title="dataClass.className + ' / Danh sách'" icon="mdi-arrow-left"
                      @onClickNavIcon="$router.back()"></header-component>
    <v-container id="wrapper">
      <v-card class="mb-10 mt-5">
        <v-banner color="pink--text" class="font-weight-bold">Học sinh mới</v-banner>
        <v-row align="center">
          <v-col cols="8" offset="1">
            <v-text-field label="Nhập tên học sinh" v-model="newStudentName"></v-text-field>
          </v-col>
          <v-col cols="2">
            <v-btn large color="pink" icon v-if="!isEdited" @click="addStudent()" class="white--text">
              <v-icon>mdi-account-plus</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-card>
      <v-card>
        <v-banner color="pink--text" class="font-weight-bold"> Danh sách học sinh</v-banner>
        <v-container id="member-list">
          <v-row v-for="student in students" :key="student.id" class="mb-1" align="center">
            <v-container>
              <v-row v-if="editedStudent && editedStudent.id === student.id" offset="1" align="center">
                <v-col cols="8" offset="1">
                  <v-text-field label="Nhập tên học sinh" v-model="student.studentName"
                                @onblur="editStudent(student)"></v-text-field>
                </v-col>
                <v-col cols="2">
                  <v-btn color="pink" raised @click="editStudent(student)" class="white--text">Lưu</v-btn>
                </v-col>
              </v-row>
              <v-row align="center" v-else>
                <v-col cols="8" class="pl-5">{{ student.studentName }}</v-col>
                <v-col cols="1">
                  <v-btn icon color="pink" v-if="!isEdited"
                         @click="isDisplayDialogConfirm = true; currentStudentId = student.id" class="white--text">
                    <v-icon>mdi-account-remove</v-icon>
                  </v-btn>
                </v-col>
                <v-col cols="1">
                  <v-btn icon color="pink" v-if="!isEdited" @click="onClickEditBtn(student)" class="white--text">
                    <v-icon>mdi-pencil</v-icon>
                  </v-btn>
                </v-col>
                <v-col cols="1">
                  <v-btn color="pink" icon v-if="!isEdited" @click="editAttendanceData(student.id)"
                         class="white--text">
                    <v-icon>mdi-calendar</v-icon>
                  </v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-row>

        </v-container>
      </v-card>
      <confirm-dialog-component :dialog.sync="isDisplayDialogConfirm" @onAgree="removeStudent(currentStudentId)"
                                title="Chắc chắn chưa ?"
                                content="Sau khi xóa dữ liệu học sinh sẽ không thể lấy lại được đâu nha !!">
      </confirm-dialog-component>
      <snackbar-component :text.sync="errorMessage"></snackbar-component>
    </v-container>
  </div>
</template>

<script>
import db from "@/script/firestore";
import ConfirmDialogComponent from "@/components/ulti/ConfirmDialogComponent";
import SnackbarComponent from "@/components/ulti/SnackbarComponent";
import HeaderComponent from "@/components/ulti/HeaderComponent";

export default {
  name: "EditStudentInfoComponent",
  components: {
    ConfirmDialogComponent,
    SnackbarComponent,
    HeaderComponent
  },
  created() {
    const classId = this.$route.params.classId;
    this.$bind('students', db.collection('chickenClass').doc(classId).collection('data'));
    this.$bind('dataClass', db.collection('chickenClass')
        .doc(classId));
  },
  data() {
    return {
      dataClass: {},
      currentStudentId: null,
      editedStudent: null,
      isEdited: false,
      students: [],
      newStudentName: '',
      errorMessage: '',
      isDisplayDialogConfirm: false
    }
  },
  computed: {
    studentsRef() {
      return db.collection('chickenClass').doc(this.$route.params.classId).collection('data');
    }
  },
  methods: {
    onClickEditBtn(student) {
      if (!this.isEdited) {
        this.editedStudent = student;
        this.isEdited = true;
      }
    },
    async editStudent(student) {
      try {
        if (student.studentName) {
          await this.studentsRef.doc(this.editedStudent.id).update({
            studentName: student.studentName
          });
          this.editedStudent = null;
          this.errorMessage = '';
          this.isEdited = false;
        } else {
          this.errorMessage = 'Name can\'t empty';
        }
      } catch (err) {
        this.errorMessage = err;
      }
    },
    async removeStudent(studentId) {
      try {
        await this.studentsRef.doc(studentId).delete();
      } catch (err) {
        this.errorMessage = err;
      }
    },
    async addStudent() {
      let isExistedStudent = this.students.find((d) => {
        return d === this.newStudentName;
      });
      if (isExistedStudent) {
        this.errorMessage = 'Học sinh này đã tồn tại rùi!';
      } else {
        try {
          if (this.newStudentName) {
            await this.studentsRef.add({
              studentName: this.newStudentName
            });
            this.newStudentName = '';
            this.errorMessage = '';
          } else {
            this.errorMessage = 'Tên học sinh không được để trống nhé!';
          }
        } catch (err) {
          this.errorMessage = err;
        }
      }
    },
    editAttendanceData(studentId) {
      const classId = this.$route.params.classId;
      this.$router.push({name: 'edit_attendance', params: {studentId: studentId, classId: classId}});
    }
  }
}
</script>

<style scoped>
#member-list {
  height: 55vh;
  overflow: scroll;
}
</style>
